 //图片放大     
function imgShow(outerdiv, img, _this){  
    //console.log(_this);
    var src = _this.src;//获取当前点击的pimg元素中的src属性    
    $(img).attr("src", src);//设置#bigimg元素的src属性   
    //console.log("src："+src);
    $(img).on("load",function(){
        var h = $(this).height();
        var w = $(this).width();
        //console.log("height:"+h);
        var top= ($(window).height()-h)/2; 
        //console.log(top);
        if(top<0){
        	$(img).css("top",0).css("margin-left",-w/2);
        }else{        	
        	$(img).css("top",top).css("margin-left",-w/2);
        }
        $(img).click(function(e){//再次点击淡出消失弹出层    
        	e.stopPropagation();
   			$(outerdiv).remove();
     	});
    });        
	$(outerdiv).click(function(){//再次点击淡出消失弹出层    
   	   $(outerdiv).remove()
	});
	
} 
export { //很关键
  imgShow
}  
