<template>
  <div class="container">
    <page-title first-title="工单管理"></page-title>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline" size="small">
        <el-form-item label="工单ID">
          <el-input v-model="searchParams.serialNumber"></el-input>
        </el-form-item>
        <el-form-item label="工单状态">
          <el-select v-model="searchParams.status">
            <el-option
              v-for="value in statusList"
              :key="value.orderid"
              :label="value.name"
              :value="value.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input v-model="searchParams.orderSn"></el-input>
        </el-form-item>
        <el-form-item v-show="role" label="商家名称">
          <el-autocomplete
            class="inline-input"
            v-model="searchParams.suppliersName"
            :fetch-suggestions="querySearch"
            placeholder="请输入内容"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="售后类型">
          <el-select v-model="searchParams.orderTypeId" clearable>
            <el-option v-for="value in orderTypeList" :key="value.id" :label="value.name" :value="value.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="申请时间">
          <el-date-picker
            @change="dataC1"
            v-model="timeinterval"
            value-format="timestamp"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
        </el-form-item>
        <div class="radio_box">
          <!--时间选择按钮：今天，昨天，近7天近30天-->
          <el-radio-group v-model="searchParams.timeType" size="mini" @change="dataC2">
            <el-radio-button v-for="value in timeList" :key="value.id" :label="value.value">{{
              value.name
            }}</el-radio-button>
          </el-radio-group>
        </div>

        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane v-for="(value, index) in statusList" :label="value.name" :key="index" :name="value.value">
          <el-table :data="list" style="width: 100%">
            <el-table-column prop="serialNumber" label="工单ID"></el-table-column>
            <el-table-column prop="createTime" label="申请时间" :formatter="dateFormat"></el-table-column>
            <el-table-column prop="status" label="工单状态" :formatter="statusType"></el-table-column>
            <el-table-column prop="orderTypeId" label="服务类型" :formatter="orderType"></el-table-column>
            <el-table-column prop="hour" label="处理时间" :formatter="chanHour"></el-table-column>
            <el-table-column prop="orderSn" label="订单号"></el-table-column>
            <el-table-column prop="suppliersName" label="商家名称"></el-table-column>

            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="viewDetail(scope.row.serialNumber)" type="text" size="small">{{
                  buttonName(scope.row.status)
                }}</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.pageSize"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { STATUSA, ORDERTYPE, DATALIST } from './static'

export default {
  name: 'WorkorderList',
  data() {
    return {
      total: 0,
      currentPage: 1,
      searchParams: {
        pageNumber: 0,
        pageSize: 10,
        timeType: '',
        serialNumber: '',
        orderSn: '',
        status: 'ALL',
        orderTypeId: ''
      },
      list: [],
      dateradio: '',
      suppliersId: '',
      activeName: 'ALL',
      statusList: STATUSA,
      orderTypeList: ORDERTYPE,
      timeList: DATALIST,
      timeinterval: '',
      role: true,
      urls: '',
      restaurants: [],
      Forstatus: this.$Data.status ? this.$Data.status : 'All'
    }
  },
  created() {
    this.userType()
  },
  computed: {
    getType() {
      let r = {}
      STATUSA.map(e => {
        r[e.orderid] = e.name
      })
      return r
    }
  },
  methods: {
    userType() {
      this.$api.get('wo/merchant/userType', null, r => {
        if (r.data.userType == '0') {
          this.urls = 'wo/platform/workOrderList?'
          this.role = true
          var arrName = []
          r.data.suppliersList.forEach(function(value) {
            let item = {}
            item['value'] = value.suppliersName
            arrName.push(item)
          })
          this.restaurants = arrName
        } else {
          this.urls = 'wo/merchant/workOrderList?'
          this.role = false
        }
        this.searchParams.status = this.Forstatus
        this.activeName = this.Forstatus
        this.getList()
      })
    },
    getList() {
      // console.log(this.searchParams.timeType)
      if (this.timeinterval.length != 0) {
        this.searchParams.startTime = this.timeinterval[0]
        this.searchParams.endTime = this.timeinterval[1]
      }
      this.replaceDatas(this.searchParams)
      // console.log(this.searchParams)
      let pramstr = JSON.stringify(this.searchParams)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get(this.urls + pramstr, null, r => {
        if (r.code == 1) {
          this.list = r.data.workOrderList
          this.total = r.data.totalElements
        } else {
          this.list = {}
          this.$message.error(r.msg)
        }
      })
    },
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    clear() {
      this.searchParams.pageNumber = 0
      this.searchParams.pageSize = 10
      this.searchParams.serialNumber = ''
      this.searchParams.order_sn = ''
      this.searchParams.orderSn = ''
      this.searchParams.status = '全部'
      this.searchParams.orderTypeId = ''
      this.searchParams.startTime = ''
      this.searchParams.endTime = ''
      this.searchParams.suppliersName = ''
      this.searchParams.timeType = ''
      this.timeinterval = ''
    },
    search() {
      this.searchParams.page = 1
      this.getList()
    },
    handleCurrentChange(currentPage) {
      this.searchParams.pageNumber = currentPage - 1
      this.getList()
    },
    handleClick() {
      this.searchParams.status = this.activeName
      this.getList()
    },
    viewDetail(id) {
      // console.log(id)
      this.$router.push({ name: 'WorkorderInfo', params: { id: id } })
    },
    // formatter
    statusType(row, column) {
      let data = row[column.property]
      let find = this.statusList.find(ele => {
        return ele.id == data
      })
      return find ? find.name : '未知状态'
    },
    orderType(row, column) {
      let data = row[column.property]
      let find = this.orderTypeList.find(ele => {
        return ele.id == data
      })
      return find ? find.name : '未知状态'
    },
    dateFormat: function(row, column) {
      let date = row[column.property] / 1000
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : '长期有效'
    },
    chanHour: function(row, column) {
      let data = row[column.property]
      return data + '小时'
    },
    dataC1() {
      this.searchParams.timeType = ''
    },
    dataC2() {
      this.timeinterval = ''
    },
    buttonName(n) {
      if (n == 2) {
        return '查看'
      } else {
        return '处理'
      }
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 10px;
      label {
        margin: 10px;
      }
    }
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.list-wrapper {
  background: #fff;
  padding: 10px;
}
.radio_box {
  background: #f1f1f1;
  border-radius: 4px;
  padding: 4px;
  display: inline-block;
  margin: 0 4px;
}
</style>
