<template>
  <div class="container">
    <page-title first-title="工单管理"></page-title>
    <section>
      <el-row :gutter="20">
        <el-col :span="16">
          <div class="workstyle">
            <p>
              工单状态：<span class="status">{{ Status(list.workOrder.status) }}</span>
            </p>
            <el-button v-if="list.workOrder.status != 2" type="primary" @click="dialogFormVisible = true"
              >添加留言</el-button
            >
          </div>
          <el-dialog title="添加留言" :visible.sync="dialogFormVisible">
            <el-form :model="addMss" :rules="rules" ref="addMss">
              <el-form-item label="回复：" prop="message">
                <el-input
                  type="textarea"
                  :rows="3"
                  maxlength="500"
                  placeholder="请输入内容"
                  v-model="addMss.message"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
            </el-form>
            <ali-upload ref="aliUpload" :names="name"></ali-upload>
            <div slot="footer" class="dialog-footer">
              <el-button
                @click="
                  resetForm('addMss')
                  dialogFormVisible = false
                "
                >放弃操作</el-button
              >
              <el-button type="primary" @click="AddMessage('addMss')">确定提交</el-button>
            </div>
          </el-dialog>
          <div class="workorder" v-for="(msg, index) in list.workOrderMessage" :key="index">
            <label>
              <img v-if="msg.icon" :src="msg.messageRole == 1 ? ProImg + msg.icon : msg.icon" alt="用户头像" />
              <img v-else src="../../assets/image/mlm-buyer-avator@3x.png" alt="默认头像" />
              <div class="cell">
                <h4>
                  {{ msg.showName }}
                  <span>{{ format(msg.createTime) }}</span>
                </h4>
                <p>{{ msg.message }}</p>
                <p v-if="msg.picArr">凭证：</p>
                <div class="imgbox">
                  <span v-for="(imgItem, imgIndex) in msg.picArr" :key="imgIndex">
                    <img :src="substring(imgItem)" alt="用户上传图片" />
                  </span>
                </div>
              </div>
            </label>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="btn_box">
            <el-button v-if="list.workOrder.status != 2" type="primary" @click="dialogFormVisible2 = true"
              >处理完成</el-button
            >
          </div>
          <el-dialog title="关闭工单" :visible.sync="dialogFormVisible2">
            <el-form :model="CloseMes" :rules="rules" ref="CloseMes">
              <el-form-item class="radios" label="选择关闭原因" prop="orderReasonTypeId">
                <el-radio-group v-model="CloseMes.orderReasonTypeId">
                  <el-radio
                    v-for="value in orderReasionType"
                    :key="value.id"
                    :label="value.name"
                    :value="value.value"
                  ></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="留言给买家：" prop="message">
                <el-input
                  type="textarea"
                  :rows="3"
                  v-model="CloseMes.message"
                  maxlength="500"
                  placeholder="请输入内容"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button
                @click="
                  resetForm('CloseMes')
                  dialogFormVisible2 = false
                "
                >取 消</el-button
              >
              <el-button type="primary" @click="submitForm('CloseMes')">确认提交</el-button>
            </div>
          </el-dialog>
          <div class="workorderDatil">
            <h4>工单信息</h4>
            <p>
              工单编号：<span>{{ list.workOrder.serialNumber }}</span>
            </p>
            <p>
              申请时间：<span>{{ dateFormat(list.workOrder.createTime) }}</span>
            </p>
            <p>
              订单编号：<span>{{ list.workOrder.orderSn }}</span>
            </p>
            <br />
            <p>
              联系人：<span>{{ list.workOrder.userName }}</span>
            </p>
            <p>
              联系方式：<span>{{ list.workOrder.mobile }}</span>
            </p>
            <p>
              服务类型：<span>{{ chanType(list.workOrder.orderTypeId) }}</span>
            </p>
            <p>申请说明：</p>
            <p>{{ list.workOrder.description }}</p>
            <div class="imgbox">
              <span v-for="(imgItem, index) in list.workOrder.picArr" :key="index">
                <img :src="substring(imgItem)" alt="用户上传图片" />
              </span>
            </div>
          </div>
        </el-col>
      </el-row>
    </section>
  </div>
</template>

<script>
import { ORDERREASIONTYPE } from './static'
import AliUpload from './ImgLoad'
import '../../assets/js/jquery-3.4.1.min.js'
import { imgShow } from '../../assets/js/bigImg.js' //注意路径

export default {
  name: 'WorkorderworkInfo',
  data() {
    return {
      wor_id: this.$route.params.id ? this.$route.params.id : '',
      loadingShow: true,
      list: {},
      dialogFormVisible: false,
      dialogFormVisible2: false,
      addMss: { message: '', pics: '' },
      CloseMes: { message: '', orderReasonTypeId: '' },
      rules: {
        message: [{ required: true, message: '留言内容不能为空', trigger: 'blur' }],
        orderReasonTypeId: [{ required: true, message: '请选择关闭原因', trigger: 'change' }]
      },
      orderReasionType: ORDERREASIONTYPE,
      urls: '',
      name: { a: '', b: '', c: '' },
      ProImg: 'https://shop.itzcdn.com/'
    }
  },
  components: {
    AliUpload
  },
  created() {
    this.getrol()
    this.userType()
  },
  mounted() {
    $(document).on('click', 'img', function(e) {
      $('.bg').show()
      $('.container').append('<div class="logoImg"><img class="bigimg" src=""/></div>')
      e.stopPropagation()
      var _this = e.currentTarget //将当前的pimg元素作为_this传入函数
      //console.log(_this)
      imgShow('.logoImg', '.bigimg', _this)
    })
  },
  methods: {
    userType() {
      this.$api.get('wo/merchant/userType', null, r => {
        // console.log(r)
        r.data.userType == '0' ? (this.urls = 'wo/platform/') : (this.urls = 'wo/merchant/')
        this.getInfo()
      })
    },
    getrol() {
      this.$api.get('wo/customer/userInfo', null, r => {
        // console.log(r)
        let n3 = r.data.indexOf('$z6d')
        let n4 = r.data.indexOf('!d2f')
        let n5 = r.data.indexOf('?#d5')
        let n6 = r.data.indexOf('!w$d')
        let n7 = r.data.indexOf('$d2g')
        let n8 = r.data.indexOf('#vhf')
        this.name.a = r.data.slice(n3 + 4, n4)
        this.name.b = r.data.slice(n5 + 4, n6)
        this.name.c = r.data.slice(n7 + 4, n8)
      })
    },
    substring(str) {
      if (str.indexOf('uploadId') != -1) {
        str = str.match(/(\S*)uploadId/)[1]
      }
      return str
    },
    Status(a) {
      if (a == 0) {
        return '待处理'
      } else if (a == 1) {
        return '处理中'
      } else {
        return '已关闭'
      }
    },
    getInfo() {
      this.$api.get(this.urls + this.wor_id, null, r => {
        if (r.code == 1) {
          this.list = r.data
        } else {
          this.list = {}
          this.$message.error(r.msg)
        }
      })
    },
    chanType(a) {
      if (a == 1) {
        return '退款'
      } else if (a == 2) {
        return '退货退款'
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.CloseOrder()
        } else {
          // console.log('error submit!!');
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    CloseOrder() {
      //  console.log(this.CloseMes)
      let a = ''
      this.CloseMes.orderReasonTypeId == '无法退款' ? (a = '2') : (a = '1')
      this.CloseMes.orderReasonTypeId = a
      let pram = {}
      pram.serialNumber = this.wor_id
      pram.message = this.CloseMes.message
      this.$api.put(this.urls + 'workOrder?orderReasonTypeId=' + this.CloseMes.orderReasonTypeId, pram, r => {
        // console.log(r)
        this.list = r
        this.dialogFormVisible2 = false
        this.getInfo()
      })
    },
    format(shijianchuo) {
      //shijianchuo是整数，否则要parseInt转换
      var time = new Date(shijianchuo)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return y + '年' + m + '月' + d + '日 ' + h + ':' + mm + ':' + s
    },
    AddMessage(a) {
      this.addMss.serialNumber = this.wor_id
      // console.log(this.addMss)
      let url = this.$refs.aliUpload.ImgUrl
      let file = this.$refs.aliUpload.files
      let pics = url.join(',')
      //    console.log(pics)
      this.addMss.pics = pics
      this.$refs[a].validate(valid => {
        if (valid) {
          if (url.length == file.length) {
            this.$api.post(this.urls + 'workOrderMessage', this.addMss, r => {
              // console.log(r)
              this.list = r
              this.$refs.aliUpload.ImgUrl = []
              this.$refs.aliUpload.files = []
              this.addMss.message = ''
              this.getInfo()
              this.dialogFormVisible = false
            })
          } else {
            this.$message('图片上传中，请稍后提交')
          }
        } else {
          // console.log('error submit!!');
          return false
        }
      })
    },
    dateFormat(date) {
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', (date / 1000) * 1e3) : '长期有效'
    }
  }
}
</script>

<style lang="scss">
section {
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.workorderDatil {
  font-size: 12px;
  background: #fff;
  border: 1px solid #ccc;
  padding-left: 20px;
  margin-top: 20px;
  p {
    color: #999;
  }
}
.container {
  font-size: 15px;
  position: relative;
  .logoImg {
    position: fixed;
    width: 100%;
    height: 100%;
    margin-right: 0px;
    margin-left: 0px;
    top: 0;
    bottom: 0;
    text-align: center;
    z-index: 10001;
    background: rgba(0, 0, 0, 0.3);
    vertical-align: middle;
    overflow: auto;
    .bigimg {
      position: absolute;
      left: 50%;
      box-sizing: border-box;
      background: #fff;
      transition: all 0.5s;
    }
  }
}
.workstyle {
  position: relative;
  margin-bottom: 30px;
  p {
    margin: 0;
    .status {
      font-size: 20px;
    }
  }
  button {
    position: absolute;
    right: 0;
    top: 0;
  }
}
.workorder {
  background: #fff;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  label {
    display: flex;
    justify-content: flex-start;
    padding: 6px;
    background: #fff;
    img {
      width: 50px;
      height: 50px;
    }
    .cell {
      padding-left: 10px;
      width: 80%;

      .imgbox img {
        width: 18%;
        height: auto;
      }
    }
    h4 {
      color: #333;
      font-size: 16px;
      margin: 6px 0;
      span {
        font-size: 12px;
        color: #999;
        display: block;
        font-weight: normal;
      }
    }
  }
}
.radios label {
  display: block;
  margin: 10px;
  font-size: 16px;
  text-align: left;
}
.imgbox {
  width: 100%;
  img {
    width: 80px;
    border: 2px solid #ccc;
    margin-right: 10px;
  }
}
</style>
